<template>
    <div class="content-wrap">
        <div class="search-wrap">
            <el-row 
                type="flex"
                :gutter="10"
                style="height:52px;">
                <el-col :span="5">
                    <span class="item-text">订单编号：</span>
                    <el-input
                        class="items"
                        placeholder="请输入订单编号"
                        v-model="orderId" />
                </el-col>
                <el-col :span="5">
                    <span class="item-text">下单人：</span>
                    <el-input
                        class="items"
                        placeholder="请输入下单人手机号"
                        v-model="telephone" />
                </el-col>
                <el-col :span="5">
                    <span class="item-text">支付金额：</span>
                    <el-input
                        class="items special"
                        placeholder="请输入最小支付金额"
                        v-model="minFee" />-
                    <el-input
                        class="items special"
                        placeholder="请输入最大支付金额"
                        v-model="maxFee" />
                </el-col>
                <el-col :span="5">
                    <span class="item-text">下单时间：</span>
                    <SelectTime class="items" ref="selectCreateTime" @selectTime="selectCreateTime" :type="'datetimerange'" />
                </el-col>
            </el-row>
            <el-row 
                type="flex"
                :gutter="10"
                style="height:52px;">
                <el-col :span="5">
                    <span class="item-text">订购类型：</span>
                    <el-select class="items" v-model="goodsName" placeholder="请选择">
                        <el-option
                            v-for="item in typeOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
                <el-col :span="5">
                    <span class="item-text">订单状态：</span>
                    <el-select class="items" v-model="orderStatusStr" placeholder="请选择">
                        <el-option
                            v-for="item in statusOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
                <el-col :span="5">
                    <span class="item-text">下单渠道：</span>
                    <el-select class="items" v-model="client" placeholder="请选择">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
                <el-button type="primary" style="height:40px" @click="search">查询</el-button>
                <el-button style="height:40px" @click="reset">重置</el-button>
            </el-row>
        </div>
        <div class="table-wrap">
            <el-table
                :data="tableData"
                border
                :header-cell-style="{textAlign: 'center'}"
                :cell-style="{ textAlign: 'center' }"
                style="width: 100%">
                    <el-table-column
                        prop="order_id"
                        label="订单编号"
                    >
                    </el-table-column>
                    <el-table-column
                        label="创建时间"
                    >
                        <template slot-scope="scope">
                            <span>{{
                                scope.row.order_create_time | getTime
                            }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="下单时间">
                        <template slot-scope="scope">
                            <span>{{
                                scope.row.order_create_time | getTime
                            }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="content"
                        label="订购内容">
                    </el-table-column>
                    <el-table-column
                        prop="pad_num"
                        label="订购数量">
                    </el-table-column>
                    <el-table-column
                        prop="service_time"
                        label="订购时长">
                    </el-table-column>
                    <el-table-column
                        prop="order_price"
                        label="支付金额">
                        <template slot-scope="scope">
                            <span>{{
                                Number(scope.row.order_price/100).toFixed(2)
                            }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="order_status_str"
                        label="订单状态">
                    </el-table-column>
                    <el-table-column
                        prop="user_tel"
                        label="下单人">
                    </el-table-column>
                    <el-table-column
                        prop="client"
                        label="下单渠道">
                    </el-table-column>
                    <el-table-column
                        label="操作">
                        <template slot-scope="scope">
							<el-button
								type="text"
								size="mini"
								@click="handleDetail(scope.row)"
								>详情</el-button
							>
						</template>
                    </el-table-column>
            </el-table>
            <Pagination 
                style="margin:10px 0 20px 0;text-align:right;"
                @handleCurrentChange="handleCurrentChange"
                @handleSizeChange="handleSizeChange"
                :total="totalNum"
                :currentPage="currentPage"
            />
        </div>
    </div>
</template>
<script>
import { getOrder } from '../../api/order';
import { parseTime } from '../../utils/index';
import { Pagination, SelectTime } from '../../components'
export default {
    data() {
        return{
            orderId: '',
            telephone: '',
            createBeginTime: '',
            createEndTime: '',
            minFee: null,
            maxFee: null,
            goodsName: '',
            typeOptions: [
                {
                    value: '云机购买',
                    label: '云机购买'
                }, {
                    value: '云机续费',
                    label: '云机续费'
                }, {
                    value: 'IP代理',
                    label: 'IP代理'
                }, {
                    value: '设备大师',
                    label: '设备大师'
                }
            ],
            orderStatusStr: '',
            statusOptions: [
                {
                    value: '等待支付',
                    label: '等待支付'
                }, {
                    value: '支付成功',
                    label: '支付成功'
                }, {
                    value: '分配成功',
                    label: '分配成功'
                },{
                    value: '续费成功',
                    label: '续费成功'
                },{
                    value: '续费失败',
                    label: '续费失败'
                },{
                    value: '订单失败',
                    label: '订单失败'
                },{
                    value: '订单失效',
                    label: '订单失效'
                },
            ],
            client: '',
            options: [{
                    value: 'websit',
                    label: 'Web'
                }, {
                    value: 'win',
                    label: 'PC'
                }, {
                    value: 'android',
                    label: 'Android'
                }, {
                    value: 'ios',
                    label: 'IOS'
                }
            ],
            tableData: [],
            currentPage: 1,
            totalNum: 0,
            pageSize: 10
        }
    },
    filters: {
        getTime(time) {
            if(time) return parseTime(time);
			return '-'
		},
    },
    // watch: {
    //     '$route' (to, from) {
    //         this.$router.go(0);
    //     }
    // },
    mounted() {
        this.getList()
    },
    methods: {
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getList();
        },
        handleSizeChange(val) {
            this.pageSize = val;
            this.getList();
        },
        search() {
            this.currentPage = 1;
            this.pageSize = 10;
            this.getList()
        },
        reset() {
            this.orderId = '';
            this.goodsName  = '';
            this.telephone = '';
            this.orderStatusStr = '';
            this.client = '';
            this.minFee = '';
            this.maxFee = '';
            this.currentPage = 1;
            this.pageSize = 10;
            this.$refs.selectCreateTime.resetTimes();
            this.getList();
        },
        selectCreateTime(val) {
            this.createBeginTime = val.startTime;
            this.createEndTime = val.endTime
        },
        getList() {
            let params = new Object();
            params.orderId = this.orderId;
            params.goodsName  = this.goodsName;
            params.telephone = this.telephone;
            params.orderStatusStr = this.orderStatusStr;
            this.createBeginTime ? params.createBeginTime = this.createBeginTime : null;
            this.createEndTime ? params.createEndTime = this.createEndTime : null;
            params.client = this.client;
            params.minFee = this.minFee ? this.minFee*100 : '';
            params.maxFee = this.maxFee ? this.maxFee*100 : '';
            params.pageNum = this.currentPage;
            params.pageSize = this.pageSize;
            getOrder(params)
            .then(res => {
                if (res.resultCode == 0) {
                    this.tableData = res.resultInfo.orderList;
                    this.totalNum = res.resultInfo.total;
                }
            })
            .catch(error => {
                console.log(error)
            });
        },
        handleDetail(v) {
            console.log(v)
            this.$router.push('/order/order-detail');
            this.$store.dispatch('order/selectedOrder', v);
        },
    },
    components: {
        Pagination, SelectTime
    }
}
</script>
<style lang="less" scoped>
.content-wrap{
    padding: 25px;
    .search-wrap{
        padding: 20px;
        border: 1px solid #ddd;
        .items{
            width: calc(100% - 100px);
        }
        .special{
            width: calc((100% - 100px)/2 - 5px);
        }
        .item-text{
            width: 80px;
            text-align: left;
            display: inline-block;
        }
    }
    .table-wrap{
        margin-top: 20px;
    }
}
</style>


